
@font-face {
  font-family: 'Icomoon';
  src: url('../fonts/icomoon/icomoon.eot');
  src: url('../fonts/icomoon/icomoon.eot?#iefix') format('embedded-opentype'),
  url('../fonts/icomoon/icomoon.woff') format('woff'),
  url('../fonts/icomoon/icomoon.ttf') format('truetype'),
  url('../fonts/icomoon/icomoon.svg#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'SimpleLineIcons';
  src: url('../fonts/simple-line-icons/Simple-Line-Icons.eot');
  src: url('../fonts/simple-line-icons/Simple-Line-Icons.eot?#iefix') format('embedded-opentype'),
  url('../fonts/simple-line-icons/Simple-Line-Icons.woff') format('woff'),
  url('../fonts/simple-line-icons/Simple-Line-Icons.ttf') format('truetype'),
  url('../fonts/simple-line-icons/Simple-Line-Icons.svg#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

span.font-icon-moon[data-icon]::before {
  font-family: 'Icomoon', sans-serif;
  content: attr(data-icon);
}

span.font-simple-line-icon[data-icon]::before {
  font-family: 'SimpleLineIcons', sans-serif;
  content: attr(data-icon);
}